@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&family=Roboto&family=Rubik:wght@400;500;600;700&display=swap');
:root {
    --color-primary: #000000ca;
    --color-secondary: #ffb003;
    --color-tertiary: #ff585f;
    --color-primary-darker: #000000dc;
    --color-secondary-darker: #444;
    --color-tertiary-darker: #fd424b;
    --color-primary-opacity: #12e643b1;
    --color-secondary-opacity: #ffcd0331;
    --color-tertiary-opacity: #dfd9d9ab;
    --gradient-primary: linear-gradient(to top left, #3478cb, #88bff3);
    --gradient-secondary: linear-gradient(to top left, #ffb003, #ffcb03);
  }
  * {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body {
  /* margin: 0 auto; */
  width: 100%;
  position:absolute;
  top: 0;
  bottom: 0;
 
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  color: #ddd;
  line-height: 1.9;
  background-color: var(--color-primary);
}


/* HEADER*/
.header{
  margin: 0 auto;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header_title{
  
    flex: 1;
    max-width: 100vw;
    display: grid;
    align-content: center;
    justify-content: center;
    font-size: 2.5rem;
}
.nav {
  margin: 0 auto;
  background-color: var(--color-secondary);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height:8vw;
  width: 100%;
  z-index: 100;
  color: #444;
}
.nav.sticky {
    background-color: rgba(255, 255, 255, 0.95);
  }
  
.nav__links{
    display: flex;
    align-items: center;
    list-style: none;
}
.nav__item {
    margin-left: 1vw;
    margin-right: 1vw;
    font-weight: 100px;
  }

.nav__link{
    color: inherit;   
    font-size: 2.5vw;
    text-decoration: none;
}
.nav__link--btn:link,
.nav__link--btn:visited {
  padding: 0.8vw 2.5vw;
  border-radius: 3vw;
  background-color: var(--color-secondary-darker);
  color: #ddd;
}
.logo{
    font: weight 400px; ;
    font-size: 6vw;
    text-decoration: none;
    color: #444;
    margin-left: 2vw;
}
.highlight__logo{
    color: #ddd;
}
.section{
  margin: 0 auto;
  width: 100%;
}
.section1{
  margin: 0 auto;
}
.section__title{
text-align: center;}